export default {
  name: "TodoList",
  data() {
    return {
      tableData: [],
      newTitle: ""
    };
  },
  mounted() {
    this.axios
      .get("/v1/todo")
      .then(response => (this.tableData = response.data));
  },
  methods: {
    tableRowClassName({ row }) {
      if (row.status) {
        return "success-row";
      } else {
        return "";
      }
    },
    getTodoList() {
      this.axios
        .get("/v1/todo")
        .then(response => (this.tableData = response.data));
    },
    handleEdit(index, row) {
      let messageSuffix = row.status ? " 置为未完成" : " 置为已完成";
      this.axios
        .put("/v1/todo/" + row.id, {
          status: !row.status
        })
        .then(() => {
          this.tableData[index].status = !row.status;
          this.$message({
            showClose: true,
            duration: 1500,
            message: `<${row.title}> ${messageSuffix}`,
            type: "success"
          });
        });
    },
    handleDelete(index, id) {
      this.axios.delete("/v1/todo/" + id).then(() => {
        this.tableData.splice(index, 1);
        this.$message({
          showClose: true,
          duration: 1500,
          message: "删除待办事项成功",
          type: "success"
        });
      });
    },
    handleAdd() {
      if (this.newTitle != "") {
        this.axios
          .post("/v1/todo", {
            title: this.newTitle
          })
          .then(() => {
            this.getTodoList();
            this.$message({
              showClose: true,
              duration: 1500,
              message: "添加待办事项成功",
              type: "success"
            });
          });
        this.newTitle = "";
      } else {
        this.$message({
          showClose: true,
          duration: 1500,
          message: "title不能为空哦",
          type: "warning"
        });
      }
    }
  }
};
